<template>
    
    <img src="https://raw.githubusercontent.com/jjhdfd31/vue/main/images/lotto_logo.PNG" alt="img">
    
    <section class="select">
        <span>1등번호 : </span>
        <div>
            <select v-model="lotto0">
                <option value="" disabled selected>번호</option>
                <option v-for="i in 45" :value="i" :key="i">{{ i }}</option>
            </select>
        </div>
        <div>
            <select v-model="lotto1">
                <option value="" disabled selected>번호</option>
                <option v-for="i in 45" :value="i" :key="i">{{ i }}</option>
            </select>
        </div>
        <div>
            <select v-model="lotto2">
                <option value="" disabled selected>번호</option>
                <option v-for="i in 45" :value="i" :key="i">{{ i }}</option>
            </select>
        </div>
        <div>
            <select v-model="lotto3">
                <option value="" disabled selected>번호</option>
                <option v-for="i in 45" :value="i" :key="i" >{{ i }}</option>
            </select>
        </div>
        <div>
            <select v-model="lotto4">
                <option value="" disabled selected>번호</option>
                <option v-for="i in 45" :value="i" :key="i">{{ i }}</option>
            </select>
        </div>
        <div>
            <select v-model="lotto5">
                <option value="" disabled selected>번호</option>
                <option v-for="i in 45" :value="i" :key="i">{{ i }}</option>
            </select>
        </div>
        <div style="padding-left:20px">
            <select v-model="lotto6" >
                <option value="" disabled selected>보너스</option>
                <option v-for="a in 45" :value="a" :key="a"  >{{ a }}</option>
            </select>
        </div>

    </section>

    <section class="lotto">
        <div :style="`background-color: ${numColor0}`">{{ num0 }}</div>
        <div :style="`background-color: ${numColor1}`">{{ num1 }}</div>
        <div :style="`background-color: ${numColor2}`">{{ num2 }}</div>
        <div :style="`background-color: ${numColor3}`">{{ num3 }}</div>
        <div :style="`background-color: ${numColor4}`">{{ num4 }}</div>
        <div :style="`background-color: ${numColor5}`">{{ num5 }}</div>
    </section>
    <section class="result"></section>

    <section class="btn">
        <button @click="returnOne(step)">재시도(1개)</button>
        <button @click="plusOne(step)">번호추출(1개)</button>
        <button @click="plusAll">번호추출(전체)</button>
        <button @click="saveNumber">번호저장</button>
    </section>

    <section v-if="saveNumberVue==1" class="text">
        <div v-for="a in lottoData" :key="a"><span>{{ a.num0 }}</span><span>{{ a.num1 }}</span><span>{{ a.num2 }}</span><span>{{ a.num3 }}</span><span>{{ a.num4 }}</span><span>{{ a.num5 }}</span></div>
    </section>

</template>

<script>
import lottoData from '../lottoData.js'



export default {
    
    name : 'lottoVue',
    
    data(){
        return {

            lottoBonus : 0,
            lottoResult : 0,
            lotto0 : '',
            lotto1 : '',
            lotto2 : '',
            lotto3 : '',
            lotto4 : '',
            lotto5 : '',
            lotto6 : '',
            numColor0 : '',
            numColor1 : '',
            numColor2 : '',
            numColor3 : '',
            numColor4 : '',
            numColor5 : '',
            num0 : '',
            num1 : '',
            num2 : '',
            num3 : '',
            num4 : '',
            num5 : '',
            step : 0,
            lottoData : lottoData,
            saveNumberVue : 0,
        };
    },


    watch : {
        
    },


    methods : {


        result(){
                if(this.lottoResult == 3){
                    document.querySelector('.result').innerHTML = "5등 당첨";
                }else if(this.lottoResult == 4){
                    document.querySelector('.result').innerHTML = "4등 당첨";
                }else if(this.lottoResult == 5 && this.lottoBonus == 1){
                    document.querySelector('.result').innerHTML = "2등 당첨";
                    this.lottoBonus = 0
                    console.log(this.lottoBonus);
                }else if(this.lottoResult == 5){
                    document.querySelector('.result').innerHTML = "3등 당첨";
                }else if(this.lottoResult == 6){
                    document.querySelector('.result').innerHTML = "1등 당첨";
                }else if(this.lottoResult < 3){
                    document.querySelector('.result').innerHTML = "";
                }
                return this.lottoResult = 0
        },

        compare(){

            if(this.lotto0 == this.num0 || this.lotto0 == this.num1 
            || this.lotto0 == this.num2 || this.lotto0 == this.num3
            ||this.lotto0 == this.num4 ||this.lotto0 == this.num5){
                this.lottoResult++;
            }
            if(this.lotto1 == this.num0 || this.lotto1 == this.num1 
            || this.lotto1 == this.num2 || this.lotto1 == this.num3
            ||this.lotto1 == this.num4 ||this.lotto1 == this.num5){
                this.lottoResult++;
            }
            if(this.lotto2 == this.num0 || this.lotto2 == this.num1 
            || this.lotto2 == this.num2 || this.lotto2 == this.num3
            ||this.lotto2 == this.num4 ||this.lotto2 == this.num5){
                this.lottoResult++;
            }
            if(this.lotto3 == this.num0 || this.lotto3 == this.num1 
            || this.lotto3 == this.num2 || this.lotto3 == this.num3
            ||this.lotto3 == this.num4 ||this.lotto3 == this.num5){
                this.lottoResult++;
            }
            if(this.lotto4 == this.num0 || this.lotto4 == this.num1 
            || this.lotto4 == this.num2 || this.lotto4 == this.num3
            ||this.lotto4 == this.num4 ||this.lotto4 == this.num5){
                this.lottoResult++;
            }
            if(this.lotto5 == this.num0 || this.lotto5 == this.num1 
            || this.lotto5 == this.num2 || this.lotto5 == this.num3
            ||this.lotto5 == this.num4 ||this.lotto5 == this.num5){
                this.lottoResult++;
            }
            if(this.lotto6 == this.num0 || this.lotto6 == this.num1 
            || this.lotto6 == this.num2 || this.lotto6 == this.num3
            ||this.lotto6 == this.num4 ||this.lotto6 == this.num5){
                this.lottoBonus++;
            }
            this.result();

            return
        },



        saveNumber(){
            var saveText = {
                num0 : this.num0,
                num1 : this.num1,
                num2 : this.num2,
                num3 : this.num3,
                num4 : this.num4,
                num5 : this.num5,
            };
            this.lottoData.unshift(saveText);
            console.log(saveText);
            this.saveNumberVue = 1;
        },




        plusOne(step){
            let oneShot0;
            let oneShot1;
            let oneShot2;
            let oneShot3;
            let oneShot4;
            let oneShot5;   
            if(step==0){
                oneShot0 = Math.ceil(Math.random() * 45);
                this.step++;
                this.num0 = oneShot0;
            }
            

            if(step==1){    //do while   do실행한다 while하는동안
                do{
                    oneShot1 =  Math.ceil(Math.random() * 45);
                    this.num1 = oneShot1;
                }while(this.num0 == this.num1);
                this.step++;
            }

            if(step==2){
                do{
                    oneShot2 =  Math.ceil(Math.random() * 45);
                    this.num2 = oneShot2;
                }while(this.num2 == this.num1 || this.num2 == this.num0);
                this.step++;
            }

            if(step==3){
                do{
                    oneShot3 =  Math.ceil(Math.random() * 45);
                    this.num3 = oneShot3;
                }while(this.num3 == this.num2 || this.num3 == this.num1 || this.num3 == this.num0);
                this.step++;
            }

            if(step==4){
                do{
                    oneShot4 =  Math.ceil(Math.random() * 45);
                    this.num4 = oneShot4;
                }while(this.num4 == this.num3 || this.num4 == this.num2 || this.num4 == this.num1 || this.num4 == this.num0);
                this.step++;
            }

            if(step==5){
                do{
                    oneShot5 =  Math.ceil(Math.random() * 45);
                    this.num5 = oneShot5;
                }while(this.num5 == this.num4 || this.num5 == this.num3 || this.num5 == this.num2 || this.num5 == this.num1 || this.num5 == this.num0);
                this.compare();
            }
            this.lottoColor();
            return
        },


        returnOne(step){
            document.querySelector('.result').innerHTML = "";
            if(step==5){
                this.num5 = '';
                this.step = 4;
            }
            if(step==4){
                this.num4 = '';
                this.step = 3;
            }
            if(step==3){
                this.num3 = '';
                this.step = 2;
            }
            if(step==2){
                this.num2 = '';
                this.step = 1;
            }
            if(step==1){
                this.num1 = '';
                this.step = 0;
            }
            if(step==0){
                this.num0 = '';
                this.step == 5;
            }
            this.lottoColor();
            return 
        },

        plusAll(){
            
            this.step=5;
            let allShot0;
            let allShot1;
            let allShot2;
            let allShot3;
            let allShot4;
            let allShot5;
            
            do{ 
                allShot0 = Math.ceil(Math.random() * 45);
                allShot1 = Math.ceil(Math.random() * 45);
                allShot2 = Math.ceil(Math.random() * 45);
                allShot3 = Math.ceil(Math.random() * 45);
                allShot4 = Math.ceil(Math.random() * 45);
                allShot5 = Math.ceil(Math.random() * 45);
                this.num0 = allShot0;
                this.num1 = allShot1;
                this.num2 = allShot2;
                this.num3 = allShot3;
                this.num4 = allShot4;
                this.num5 = allShot5;
            }while(
                this.num5 == this.num4 || this.num5 == this.num3 || this.num5 == this.num2 || this.num5 == this.num1 || this.num5 == this.num0 ||
                this.num4 == this.num3 || this.num4 == this.num2 || this.num4 == this.num1 || this.num4 == this.num0 ||
                this.num3 == this.num2 || this.num3 == this.num1 || this.num3 == this.num0 ||
                this.num2 == this.num1 || this.num2 == this.num0 ||
                this.num1 == this.num0
            );
            this.lottoColor();
            this.compare();
            return
        },

        lottoColor(){
            if( 0<this.num0 && this.num0< 11){
                this.numColor0 = 'yellow';
            }else if( 10<this.num0 && this.num0< 21){
                this.numColor0 = 'skyblue';
            }else if( 20<this.num0 && this.num0< 31){
                this.numColor0 = 'red';
            }else if( 30<this.num0 && this.num0< 41){
                this.numColor0 = 'gray';
            }else if( 40<this.num0){
                this.numColor0 = 'green';
            }else(this.numColor0 = '')

            if( 0<this.num1 && this.num1< 11){
                this.numColor1 = 'yellow';
            }else if( 10<this.num1 && this.num1< 21){
                this.numColor1 = 'skyblue';
            }else if( 20<this.num1 && this.num1< 31){
                this.numColor1 = 'red';
            }else if( 30<this.num1 && this.num1< 41){
                this.numColor1 = 'gray';
            }else if( 40<this.num1){
                this.numColor1 = 'green';
            }else(this.numColor1 = '')

            if( 0<this.num2 && this.num2< 11){
                this.numColor2 = 'yellow';
            }else if( 10<this.num2 && this.num2< 21){
                this.numColor2 = 'skyblue';
            }else if( 20<this.num2 && this.num2< 31){
                this.numColor2 = 'red';
            }else if( 30<this.num2 && this.num2< 41){
                this.numColor2 = 'gray';
            }else if( 40<this.num2){
                this.numColor2 = 'green';
            }else(this.numColor2 = '')

            if( 0<this.num3 && this.num3< 11){
                this.numColor3 = 'yellow';
            }else if( 10<this.num3 && this.num3< 21){
                this.numColor3 = 'skyblue';
            }else if( 20<this.num3 && this.num3< 31){
                this.numColor3 = 'red';
            }else if( 30<this.num3 && this.num3< 41){
                this.numColor3 = 'gray';
            }else if( 40<this.num3){
                this.numColor3 = 'green';
            }else(this.numColor3 = '')

            if( 0<this.num4 && this.num4< 11){
                this.numColor4 = 'yellow';
            }else if( 10<this.num4 && this.num4< 21){
                this.numColor4 = 'skyblue';
            }else if( 20<this.num4 && this.num4< 31){
                this.numColor4 = 'red';
            }else if( 30<this.num4 && this.num4< 41){
                this.numColor4 = 'gray';
            }else if( 40<this.num4){
                this.numColor4 = 'green';
            }else(this.numColor4 = '')

            if( 0<this.num5 && this.num5< 11){
                this.numColor5 = 'yellow';
            }else if( 10<this.num5 && this.num5< 21){
                this.numColor5 = 'skyblue';
            }else if( 20<this.num5 && this.num5< 31){
                this.numColor5 = 'red';
            }else if( 30<this.num5 && this.num5< 41){
                this.numColor5 = 'gray';
            }else if( 40<this.num5){
                this.numColor5 = 'green';
            }else(this.numColor5 = '')
            return
        },

    }

    
}   



</script>

<style>
    
    img{width: 800px; height: auto;}
    section.select{
        height: 35px;
        background: url('') calc(100% - 5px) center no-repeat;
        background-size: 20px;
        padding: 5px 30px 5px 10px;
        border-radius: 4px;
        outline: 0 none;
        display: flex;margin: auto;width: 600px}
    section.select div{width: 60px;line-height: 35px;}
    section.select span{font-size: 25px;padding: 0 15px;}
    section.select option {background: green;color: #fff;padding: 3px 0;text-align: center;}
    section.lotto{display: inline-flex;margin-top: 30px;}
    section.lotto div {
        margin: 10px;
        width: 100px;height: 100px;
        text-align: center;line-height: 100px;
        font-size: 30px;
        border-radius: 100%;
        background-color: yellowgreen;
        font-weight: bold;
    }
    section.result {
        height: 40px;
        margin-top: 30px;
        font-size: 20px;
        color: red;
    }
    section.btn {margin-top: 20px;}

    section.btn button{
        width: 120px;height: 30px;
        margin: 20px;
    }
    section.text div{
        width: 450px;
        margin: auto;
        margin-top: 20px;
        margin-bottom: 10px;
    }
    section.text div span{
        display: inline-block;
        width: 70px;
        font-size: 35px;
        color: #fff;
        background-color: green;
    }

</style>